.hero-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(135deg, #6f86d6, #48c6ef);
    color: white;
    text-align: center;
    padding: 20px;
  }
  
  .hero-section h1 {
    font-size: 3em;
    margin: 0;
  }
  
  .hero-section p {
    font-size: 1.5em;
    margin: 10px 0;
  }
  
  .hero-section .skills {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20px;
  }
  
  .hero-section .skill {
    background-color: rgba(255, 255, 255, 0.1);
    color: white;
    border-radius: 20px;
    padding: 10px 20px;
    margin: 5px;
    transition: background-color 0.3s;
  }
  
  .hero-section .skill:hover {
    background-color: rgba(255, 255, 255, 0.2);
  }